<template>
    <transition name="pl-context-menu">
        <div class="pl-context-menu" :style="styles" v-show="show">
            <div class="pl-context-menu-content" :style="contentStyles">
                <pl-scroll>
                    <div class="pl-context-menu-item"
                         :class="{'pl-context-menu-item-active':p_isActive(item)}"
                         v-for="(item,index) in data"
                         :key="index"
                         v-if="!!data"
                         @click="p_clickItem(item)">
                        <span>{{!!labelKey?item[labelKey]:item}}</span>
                    </div>
                </pl-scroll>
            </div>
            <div class="pl-context-menu-footer" v-show="multiple">
                <div class="pl-context-menu-footer-content">
                    <pl-button label="" @click="p_confirm"/>
                </div>
            </div>
        </div>
    </transition>
</template>

<script>
    import PlScroll from "../scroll/pl-scroll";
    import PlButton from "../button/pl-button";

    export default {
        name: "pl-context-menu",
        components: {PlButton, PlScroll},
        data() {
            return {
                data: [],                                       //
                value: null,                                    //
                labelKey: null,                                 //key
                valueKey: null,                                 //key
                multiple: null,                                 //
                width: null,                                    //
                height: null,                                   //
                el: null,                                       //dom
                event: null,                                    //
                onConfirm: null,                                //

                show: false,                                    //
                defaultWidth: '150px',                          //
                defaultHeight: '200px',                         //
                p_top: null,                                    //
                p_left: null,                                   //
                p_targetWidth: null,                            //
                p_targetHeight: null,                           //
                p_targetTop: null,                              //
                p_targetLeft: null,                             //
            }
        },
        watch: {
            el: {
                immediate: true,
                handler(el) {
                    if (!el) return
                    this.p_saveTargetData(el)
                    this.p_top = this.p_targetTop + this.p_targetHeight
                    this.p_left = this.p_targetLeft
                },
            },
            event: {
                immediate: true,
                handler(e) {
                    if (!e) return
                    this.p_saveTargetData(e.target)
                    this.p_top = e.clientY
                    this.p_left = e.clientX
                },
            },
            show(val) {
                if (!val) this.p_clearPosition()
            },
        },
        computed: {
            styles() {
                return {
                    top: `${this.p_top}px`,
                    left: `${this.p_left}px`,
                }
            },
            contentStyles() {
                return {
                    width: this.width || this.defaultWidth,
                    height: this.height || this.defaultHeight,

                }
            },
        },
        mounted() {
            document.body.addEventListener('click', this.p_clickWindow)
        },
        methods: {
            /**
             * 
             * @author  
             * @date    2019/3/4 09:54
             */
            p_clickWindow(e) {
                if (!this.$el.contains(e.target)) {
                    this.show = false
                }
            },
            /**
             * 
             * @author  
             * @date    2019/3/4 09:54
             */
            p_clickItem(item) {
                if (!this.multiple) {
                    !!this.onConfirm && this.onConfirm(item)
                    this.show = false
                } else {
                    const ret = [].concat(this.value || [])
                    if (this.$plain.$utils.findOne(ret, (it) => !!this.valueKey ? it[this.valueKey] === item[this.valueKey] : it === item)) {
                        this.$plain.$utils.removeSome(ret, it => !!this.valueKey ? it[this.valueKey] === item[this.valueKey] : it === item)
                    } else {
                        ret.push(item)
                    }
                    this.value = ret
                }
            },
            /**
             * dom
             * @author  
             * @date    2019/3/4 09:54
             */
            p_saveTargetData(el) {
                this.p_targetWidth = el.offsetWidth
                this.p_targetHeight = el.offsetHeight
                const {top, left} = el.getBoundingClientRect()
                this.p_targetTop = top
                this.p_targetLeft = left
            },
            /**
             * 
             * @author  
             * @date    2019/3/4 10:09
             */
            p_isActive(item) {
                if (!this.value) return false
                if (!this.multiple) {
                    return !!this.valueKey ? (item[this.labelKey] === this.value) : (item === this.value)
                } else {
                    return !!this.valueKey ? this.value.indexOf(item[this.labelKey]) > -1 : (this.value.indexOf(item) > -1)
                }
            },
            /**
             * 
             * @author  
             * @date    2019/3/4 10:10
             */
            p_clearPosition() {
                this.p_top = null
                this.p_left = null
                this.p_targetTop = null
                this.p_targetLeft = null
                this.p_targetWidth = null
                this.p_targetHeight = null
            },
            /**
             * 
             * @author  
             * @date    2019/3/4 10:58
             */
            p_confirm() {
                !!this.onConfirm && this.onConfirm(this.value)
                this.show = false
            },
        },
        beforeDestroy() {
            document.body.removeEventListener('click', this.p_click)
            this.show = false
        },
    }
</script>

<style lang="scss">
    .pl-context-menu {
        @include transition-all;
        @include public-style;
        box-shadow: 0 0 15px 1px #ddd;
        position: fixed;
        left: 0;
        top: 0;
        background-color: white;
        z-index: 9999;
        .pl-context-menu-content {
            padding: 4px 0;
        }

        .pl-context-menu-item {
            padding: 4px 10px;
            font-size: 12px;
            cursor: pointer;
            &:hover, &.pl-context-menu-item-active {
                background-color: $color-primary;
                color: white;
            }
        }
        .pl-context-menu-footer {
            height: 40px;
            position: relative;
            &:after {
                position: absolute;
                top: 0;
                left: 0;
                right: 0;
                height: 3px;
                content: '';
                box-shadow: 0 0 15px 1px #ddd;
            }
            .pl-context-menu-footer-content {
                background-color: white;
                height: 100%;
                display: flex;
                justify-content: flex-end;
                align-items: center;
                padding: 0 12px;
                position: relative;
                z-index: 1;
            }
        }
    }

    .pl-context-menu-enter-active, .pl-context-menu-leave-active {
        opacity: 1;
    }

    .pl-context-menu-enter, .pl-context-menu-leave-to {
        opacity: 0;
    }
</style>