import $utils from '../../utils/utils'

const props = {
    title: {type: String},                                                                                          //
    field: {type: String},                                                                                          //
    width: {default: '200px'},                                                                                      //
    fit: {type: Number, default: 0},                                                                                //
    order: {type: Number, default: 0},                                                                              //
    fixed: {type: String, default: 'center', validator: val => $utils.oneOf(val, ['left', 'center', 'right'])},     //
    placeLeft: {type: Boolean},                                                                                     //
    placeRight: {type: Boolean},                                                                                    //
    hide: {type: Boolean},                                                                                          //
    disabledConfig: {type: Boolean},                                                                                //
    align: {type: String, default: 'left'},                                                                         //

    search: {type: Boolean, default: true},                                                                         //
    searchField: {type: Boolean},                                                                                   //
    sort: {type: Boolean, default: true},                                                                           //
    sortField: {type: Boolean},                                                                                     //
    editable: {type: Boolean, default: true},                                                                       //
    editableFunc: {type: Function},                                                                                 //

    dataType: {type: String},                                                                                       //:tel,cny,money,percent
    formatter: {type: Function},                                                                                    //
    tooltip: {type: Boolean},                                                                                       //tooltip
    link: {type: Boolean},                                                                                          //
    clickWhenIneditable: {type: Boolean, default: true},                                                            //
    showInDialog: {type: Boolean},                                                                                  //dialog

    required: {type: Boolean},                                                                                      //
    rules: {type: Array},                                                                                           //
    validFunc: {type: Function},                                                                                    //
    validOnInit: {type: Boolean},                                                                                   //
    requiredFunc: {type: Function},                                                                                 //

    quickFilter: {type: Boolean, default: false},                                                                   //
    filterName: {type: String, default: 'input'},                                                                   //
    filterOption: {type: Object},                                                                                   //
    lov: {type: String},                                                                                            //

    /**/
    scopedSlots: {type: Object},                                                                                    //
    renderNormal: {type: Object},                                                                                   //:
    renderEdit: {type: Object},                                                                                     //
    renderHead: {type: Object},                                                                                     //
    propData: {type: Object},                                                                                       //
}

const refreshProps = [
    'fit',
    'order',
    'fixed,',
    'placeLeft',
    'placeRight',
    'hide',
    'disabledConfig',
    'scopedSlots',
    'renderNormal',
    'renderEdit',
    'renderHead',
]

const BaseColumnMixin = {
    props,
    computed: {
        columnBinding() {
            return Object.keys(props).reduce((ret, key) => {
                ret[key] = this[key]
                return ret
            }, {})
        },
    },
    methods: {
        /**
         * 
         * @author  
         * @date    2019/1/10 17:45
         */
        p_clickItem({row, editRow, rowIndex, field, editable}) {
            if (!editable && this.showInDialog) this.$dialog.show(row[field])
            if (!!this.clickWhenIneditable && !!editable) return
            this.$emit('click', {row, editRow, rowIndex, field, editable})
        },
    }
}

class TableColumn {
    constructor(context) {
        const originalProps = Object.keys(props).reduce((ret, key) => {
            // context.$set(ret, key, context[key])
            ret[key] = context[key]
            return ret
        }, {})
        originalProps.width = context.$plain.$utils.removePx(originalProps.width)
        this.originalProps = originalProps
        Object.assign(this, originalProps)
    }
}

const BasicColumnGroupMixin = {
    props: {
        title: {},                                  //
        order: {type: Number, default: 0},          //
        fixed: {type: String, default: 'center'},   //
        placeLeft: {type: Boolean},                 //
        placeRight: {type: Boolean},                //
        isCtrlGroup: {type: Boolean},               //
    },
}

class TableColumnGroup {
    constructor(context) {
        const originalProps = Object.keys(BasicColumnGroupMixin.props).reduce((ret, key) => {
            // context.$set(ret, key, context[key])
            ret[key] = context[key]
            return ret
        }, {})
        this.originalProps = originalProps
        Object.assign(this, originalProps)
        this.group = true
    }
}

export {
    refreshProps,
    BaseColumnMixin,
    BasicColumnGroupMixin,
    TableColumn,
    TableColumnGroup,
}