const NAV_STORAGE_KEY = {
    TAB: 'nav-tab',
    PAGE: 'nav-page',
    BLANK: 'nav-blank',
    HISTORY: 'nav-history',

}

class Page {
    path = null;                //
    param = null;               //
    frame = false;              //
    props = null;               //

    id = null;                  //id
    init = false;               //
    component = null;           //

    constructor(page) {
        Object.assign(this, page)
    }

    saveData() {
        return {
            id: this.id,
            path: this.path,
            param: this.param,
            frame: this.frame,
            props: this.props,
        }
    }
}

class Tab {
    id = null;
    title = null;

    path = null;
    param = null;
    frame = null;
    props = null;
    storage = true;

    init = false;

    constructor(tab) {
        Object.assign(this, tab)
    }

    saveData() {
        return {
            id: this.id,
            title: this.title,
            path: this.path,
            param: this.param,
            frame: this.frame,
            props: this.props,
            storage: this.storage,
        }
    }
}

export {
    NAV_STORAGE_KEY,
    Page,
    Tab,
}