import fecha from 'fecha'
import DM from 'deepmerge'
import keyboard from './keyboard'

const SPECIAL_CHARS_REGEXP = /([:\-_]+(.))/g;
const MOZ_HACK_REGEXP = /^moz([A-Z])/;

/**
 * 
 * @author 
 * @date 2018/11/14
 */
function getKebabCase(str) {
    return str.replace(/[A-Z]/g, function (i) {
        return '-' + i.toLowerCase();
    })
}

/**
 * js
 * @author  
 * @date    2018/11/30 09:46
 */
function addScript(scriptPath) {
    const script = document.createElement("script");
    script.setAttribute("type", "text/javascript");
    script.setAttribute("src", scriptPath);
    const heads = document.getElementsByTagName("head");
    if (heads.length)
        heads[0].appendChild(script);
    else
        document.documentElement.appendChild(script);
}

/**
 * 
 * @author 
 * @date 2018/11/19
 */
function insertSort(arr, func) {
    let len = arr.length;
    for (let i = 1; i < len; i++) {
        let temp = arr[i];
        /**/
        let j = i - 1;
        /**/
        while (j >= 0 && func(arr[j], temp)) {
            /**/
            arr[j + 1] = arr[j];
            j--;
        }
        arr[j + 1] = temp;
    }
    return arr
}


/**
 * 
 * @author 
 * @date 2018/11/15
 */
function delay(duration = 100) {
    return new Promise(rs => setTimeout(() => rs(), duration))
}

/**
 * 
 * @author 
 * @date 2018/11/19
 */
function removeFromArray(array, target) {
    const index = array.indexOf(target)
    index > -1 && array.splice(index, 1)
}

/**
 * 
 * @author 
 * @date 2018/11/19
 */
function uuid() {
    let s = [];
    let hexDigits = '0123456789abcdef';
    for (let i = 0; i < 36; i++) {
        s[i] = hexDigits.substr(Math.floor(Math.random() * 0x10), 1);
    }
    s[14] = '4';                                        // bits 12-15 of the time_hi_and_version field to 0010
    s[19] = hexDigits.substr((s[19] & 0x3) | 0x8, 1);   // bits 6-7 of the clock_seq_hi_and_reserved to 01
    s[8] = s[13] = s[18] = s[23] = '-';

    let uuid = s.join('');
    return uuid;
}

/**
 * 0
 * @author 
 * @date 2018/11/19
 */
function zeroize(value, length = 2) {
    let val = value + '';
    let i = length - val.length;
    while (i > 0) {
        val = '0' + val;
        i--;
    }
    return val;
}

/**
 * 
 * @author 
 * @date 2018/11/19
 */
function camelCase(name) {
    return name
        .replace(SPECIAL_CHARS_REGEXP, function (_, separator, letter, offset) {
            return offset ? letter.toUpperCase() : letter;
        }).replace(MOZ_HACK_REGEXP, 'Moz$1');
}

/**
 * 
 * @author weishengjian
 * @date 2018/11/19
 */
export function deepCopy(data) {
    let o;
    switch (typeOf(data)) {
        case 'array':
            o = data.map(item => deepCopy(item))
            break
        case 'object':
            o = {}
            Object.keys(data).forEach(key => o[key] = deepCopy(data[key]))
            break
        case 'date':
            o = new Date()
            o.setTime(data.getTime())
            break
        case 'regExp':
            let pattern = data.valueOf();
            let flags = '';
            flags += pattern.global ? 'g' : '';
            flags += pattern.ignoreCase ? 'i' : '';
            flags += pattern.multiline ? 'm' : '';
            return new RegExp(pattern.source, flags);
        default:
            o = data;
    }
    return o;
}

/**
 * px,
 * @author 
 * @date 2018/11/19
 */
function removePx(value) {
    return ((value + '').replace(/px/g, '') - 0);
}

/**
 * 
 * @author 
 * @date 2018/11/19
 */
function typeOf(obj) {
    const toString = Object.prototype.toString;
    const map = {
        '[object Boolean]': 'boolean',
        '[object Number]': 'number',
        '[object String]': 'string',
        '[object Function]': 'function',
        '[object Array]': 'array',
        '[object Date]': 'date',
        '[object RegExp]': 'regExp',
        '[object Undefined]': 'undefined',
        '[object Null]': 'null',
        '[object Object]': 'object'
    };
    return map[toString.call(obj)];
}

/**
 * 
 * @author 
 * @date 2018/11/19
 */
function oneOf(val, array) {
    for (let i = 0; i < array.length; i++) {
        const item = array[i];
        if (item === val) return true;
    }
    return false;
}

/**
 * 
 * @author  
 * @date    2018/12/13 10:29
 * @param date 
 * @param format 
 */
function dateFormat(date, format) {
    if (date == null) return null
    format = format || 'YYYY-MM-DD';
    return fecha.format(date, format)
}

/**
 * 
 * @author  
 * @date    2018/12/13 10:30
 * @param string 
 * @param format 
 */
function dateParse(string, format) {
    if (string == null) return null
    format = format || 'YYYY-MM-DD';
    return fecha.parse(string, format)
}

/**
 * 
 * @author  
 * @date    2019/3/11 20:24
 */
function decodeDate(date) {
    const ret = {
        year: null,
        month: null,
        day: null,
        hour: null,
        minute: null,
        second: null,
        date: null,
        time: null,
        timeString: null,
    }
    if (!date) return ret
    ret.year = date.getFullYear()
    ret.month = date.getMonth()
    ret.day = date.getDate()
    ret.hour = date.getHours()
    ret.minute = date.getMinutes()
    ret.second = date.getSeconds()
    ret.date = date
    ret.time = date.getTime()
    ret.timeString = `${zeroize(ret.hour)}:${zeroize(ret.minute)}:${zeroize(ret.second)}`
    return ret
}

/**
 * px
 * @author  
 * @date    2019/1/10 10:56
 */
function unit(data) {
    const type = typeOf(data)
    switch (type) {
        case 'string':
            return data;
        case 'number':
            return `${data}px`
        default:
            return null
    }
}

/**
 * 
 * @author  
 * @date    2019/1/10 10:56
 */
function shuffle(array) {
    array = deepCopy(array)
    let currentIndex = array.length;
    let temporaryValue, randomIndex;
    // While there remain elements to shuffle...
    while (0 !== currentIndex) {
        // Pick a remaining element...
        randomIndex = Math.floor(Math.random() * currentIndex);
        currentIndex -= 1;
        // And swap it with the current element.
        temporaryValue = array[currentIndex];
        array[currentIndex] = array[randomIndex];
        array[randomIndex] = temporaryValue;
    }
    return array
}

/**
 * 
 * @author  
 * @date    2019/1/10 10:56
 */
function findOne(array, fn) {
    for (let i = 0; i < array.length; i++) {
        const item = array[i];
        if (!!fn(item)) return item
    }
    return null
}

/**
 * 
 * @author  
 * @date    2019/1/10 10:57
 */
function findSome(array, fn) {
    const ret = []
    for (let i = 0; i < array.length; i++) {
        const item = array[i];
        if (!!fn(item)) ret.push(item)
    }
    return ret
}

/**
 * 
 * @author  
 * @date    2019/4/1 19:55
 */
function indexOf(array, fn) {
    for (let i = 0; i < array.length; i++) {
        const item = array[i];
        if (!!fn(item)) return i
    }
    return null
}

/**
 * 
 * @author  
 * @date    2019/3/4 10:35
 */
function removeSome(array, fn) {
    for (let i = 0; i < array.length; i++) {
        const item = array[i];
        if (fn(item)) {
            array.splice(i, 1)
            i--
        }
    }
}

/**
 * desc 
 * @author huyang
 * @date 2018/12/11 13:43
 * @params
 */
export function telFormat(d) {
    if (d == null) return ''
    d = String(d);
    d = d.trim();
    if (d.length >= 11) {
        let d1 = d.substr(d.length - 4, 4);
        let d2 = d.substr(d.length - 8, 4);
        let d3 = d.substr(d.length - 11, 3);
        return d3 + ' ' + d2 + ' ' + d1;
    } else {
        return d;
    }
}

/**
 * desc 
 * @author huyang
 * @date 2018/12/11 13:45
 * @params
 */
export function moneyFormat(val) {
    if (val || val == 0) {
        val = val.toString().replace(/\$|\, /g, '');
        if (isNaN(val)) {
            val = '0';
        }
        const sign = val == (val = Math.abs(val));
        val = Math.floor(val * 100 + 0.50000000001);
        let cents = val % 100;
        val = Math.floor(val / 100).toString();
        if (cents < 10) {
            cents = '0' + cents;
        }
        for (let i = 0; i < Math.floor((val.length - (1 + i)) / 3); i++) {
            val = val.substring(0, val.length - (4 * i + 3)) + ',' + val.substring(val.length - (4 * i + 3));
        }
        return (sign ? '' : '-') + val + '.' + cents;
    }
}

/**
 * desc 
 * @author huyang
 * @date 2018/12/11 13:47
 * @params
 */
export function cnyFormat(d) {
    // 
    if (!d) {
        return '0';
    }
    if (isNaN(d)) {
        return '';
    }
    let dd = parseFloat(d);
    return '' + String(dd.toFixed(2)).replace(/\d{1,3}(?=(\d{3})+(\.\d*)?$)/g, '$&,');
}

/**
 * desc 
 * @author huyang
 * @date 2018/12/11 13:47
 * @params
 */
export function percentNumFormat(d) {
    //
    if (d == null) {
        return "";
    }
    if (isNaN(d)) {
        return "";
    }
    return parseFloat(d).toFixed(4) * 100 + '%';
}

/*
 *  
 *  @author     martsforever
 *  @datetime   2019/3/20 20:51
 */
function deepmerge(...args) {
    return DM(...args)
}

/**
 * url
 * @author  
 * @date    2019/4/2 10:06
 */
function encodeUrl(url, data) {
    url = url.indexOf('?') < 0 ? '?' : '&';
    let paramString = ''
    Object.keys(data).forEach(key => {
        let value = data[key] != null ? data[key] : '';
        paramString += `&${key}=${encodeURIComponent(value)}`;
    })
    paramString = paramString.length > 1 ? paramString.substr(1) : ''
    return url + paramString;
}

/**
 * url
 * @author  
 * @date    2019/4/2 10:48
 */
function decodeUrl(url) {
    let param = {}
    let pureUrl
    let urlParts = url.split('?')
    if (urlParts.length === 0) {
        console.error("" + url)
        return null
    }
    pureUrl = urlParts[0]
    if (urlParts.length === 2) {
        const paramStrings = urlParts[1].split("&");
        paramStrings.forEach((str) => {
            const [key, value] = str.split('=')
            param[key] = decodeURIComponent(value)
        })
    }
    return {url: pureUrl, param};
}

const $utils = {
    getKebabCase,                               //
    camelCase,                                  //
    addScript,                                  //js
    insertSort,                                 //
    delay,                                      //
    dateFormat,                                 //
    dateParse,                                  //
    decodeDate,                                 //
    removeFromArray,                            //
    uuid,                                       //
    zeroize,                                    //0
    deepCopy,                                   //
    removePx,                                   //px,
    typeOf,                                     //
    oneOf,                                      //
    unit,                                       //+px

    shuffle,                                    //
    findOne,                                    //
    findSome,                                   //
    indexOf,                                    //
    removeSome,                                 //

    telFormat,                                  //
    moneyFormat,                                //
    cnyFormat,                                  //
    percentNumFormat,                           //
    keyboard,                                   //
    deepmerge,                                  //
    encodeUrl,
    decodeUrl,
}

export default $utils