import PlNoticeContainer from './pl-notice-container'
import utils from '../../utils/utils'
import dom from '../../utils/dom'

class NoticeService {
    Vue;
    _el = null;
    containers = {}

    constructor(Vue){
        this.Vue = Vue
    }

    get el() {
        if (!this._el) {
            this._el = document.createElement('div')
            dom.addClass(this._el, 'pl-notice-containers')
            document.body.appendChild(this._el)
        }
        return this._el
    }

    newInstance(horizontal, vertical) {
        const instance = new this.Vue(PlNoticeContainer).$mount()
        instance.p_horizontal = horizontal
        instance.p_vertical = vertical
        this.el.appendChild(instance.$el)
        return instance
    }

    /**
     * 
     * @author  
     * @date    2018/12/24 09:58
     * @param id                    id
     * @param horizontal            
     * @param vertical              
     * @param type                  
     * @param time                  
     * @param done                  
     * @param click                 
     * @param message               
     * @param title                 
     * @param noHeader              
     * @param renderFunc            
     */
    show(message, {
        id,
        horizontal = 'end',
        vertical = 'start',
        type = 'info',
        time = 3000,
        done,
        click,
        title = '',
        noHeader = false,
        renderFunc = null,
    } = {}) {
        const position = `${horizontal}-${vertical}`
        const container = this.containers[position] || this.newInstance(horizontal, vertical)
        if (!this.containers[position]) this.containers[position] = container
        const messageOption = {
            id: id || utils.uuid(),
            text: message,
            type,
            time,
            done,
            click,
            horizontal,
            vertical,
            title,
            noHeader,
            renderFunc,
        }
        this.Vue.prototype.$nextTick(() => container.add(messageOption))
        return messageOption
    }

    close(messageOption) {
        this.containers[`${messageOption.horizontal}-${messageOption.vertical}`].remove(messageOption)
    }
}

export {
    NoticeService
}