export const ValueMixin = {
    props: {
        value: {},
    },
    watch: {
        value(val) {
            this.p_watchValue && val !== this.currentValue && (this.currentValue = val)
        },
        currentValue(val) {
            this.p_watchCurrentValue && this.$emit('input', val)
        },
    },
    data() {
        return {
            currentValue: this.value,
            p_watchValue: true,
            p_watchCurrentValue: true,
        }
    },
}

export const ValidMixin = {
    props: {
        required: {type: Boolean},                                      //
        rules: {type: Array},                                           //
        validFunc: {type: Function},                                    //
        validOnInit: {type: Boolean},                                   //
    }
}

export const BoxMixin = {
    mixins: [ValueMixin, ValidMixin],
    props: {
        /*---------------------------------------box props-------------------------------------------*/
        boxType: {type: String, default: 'fill',},                      //
        boxColor: {type: String, default: 'primary'},                   //
        boxShape: {type: String, default: 'none'},                      //
        boxSize: {type: String, default: 'default'},                    //

        prefixIcon: {type: String},                                     //
        suffixIcon: {type: String},                                     //
        clearIcon: {type: String},                                      //hovertruevaluehoveringpad-closeclearIcon
        clearable: {type: Boolean, default: true},                      //
        loading: {type: Boolean},                                       //loading 
        iconOnly: {type: Boolean},                                      //true

        readonly: {type: Boolean},                                      //
        disabled: {type: Boolean},                                      //disabled

        padding: {type: Boolean, default: true},                        //
        long: {type: Boolean,},                                         //
        width: {type: Number, default: 200},                            //
        hover: {type: Boolean},                                         //hover

        value: {},                                                      //
        required: {type: Boolean},                                      //
        rules: {type: Array},                                           //
        validOnInit: {type: Boolean},                                   //
    },
    computed: {
        boxBinding() {
            return {
                boxType: this.boxType,
                boxColor: this.boxColor,
                boxShape: this.boxShape,
                boxSize: this.boxSize,
                prefixIcon: this.prefixIcon,
                suffixIcon: this.suffixIcon,
                clearIcon: this.clearIcon,
                clearable: this.clearable,
                loading: this.loading,
                iconOnly: this.iconOnly,
                readonly: this.readonly,
                disabled: this.disabled,
                padding: this.padding,
                long: this.long,
                width: this.width,
                hover: this.hover,
                value: this.value,
                required: this.required,
                rules: this.rules,
                validOnInit: this.validOnInit,
            }
        },
    },
}

export const MountedMixin = {
    data() {
        return {
            p_mounted: false
        }
    },
    mounted() {
        this.p_mounted = true
    },
    beforeDestroy() {
        this.p_mounted = false
    },
}

export const InputMixin = {
    mixins: [BoxMixin],
    props: {
        boxType: {default: 'line'},                                 //
        boxColor: {default: 'info'},                                //
        clearIcon: {default: 'none'},                               //

        replaceRegexp: {type: RegExp},                              ///[^\-0-9.]/g
        type: {type: String, default: 'text'},                      //inputtypepassword
        focusOnHover: {type: Boolean},                              //
        defaultClear: {type: Boolean, default: true},               //input
        placeholder: {type: String, default: '...'},         //
        keyboard: {type: Boolean,},                                 //backenterup
    },
    computed: {
        inputBinding() {
            return Object.assign(this.boxBinding, {
                replaceRegexp: this.replaceRegexp,
                type: this.type,
                focusOnHover: this.focusOnHover,
                defaultClear: this.defaultClear,
                placeholder: this.placeholder,
                keyboard: this.keyboard,
            })
        },
    },
}



