const POPOVER_TRIGGER = {
    CLICK: 'click',
    HOVER: 'hover',
}
const POPOVER_DIRECTION = {
    TOP: 'top',
    BOTTOM: 'bottom',
    LEFT: 'left',
    RIGHT: 'right',
}
const POPOVER_ALIGN = {
    START: 'start',
    CENTER: 'center',
    END: 'end',
}

const PopoverMixin = {
    props: {
        trigger: {type: String, default: POPOVER_TRIGGER.CLICK},                //hover|click
        disabledHideOnClickOutside: {type: Boolean},                            //
        disabledDestroyOnHide: {type: Boolean},                                 //
        disabledToggleOnClickReference: {type: Boolean},                        //|
        initialized: {type: Boolean},                                           //
        direction: {type: String, default: POPOVER_DIRECTION.BOTTOM},           //top|bottom|left|right
        align: {type: String, default: POPOVER_ALIGN.START},                    //
        animate: {type: String, default: 'drop'},                               //
        arrow: {type: Boolean},                                                 //
        offset: {type: Number},                                                 //
        height: {default: 180},                                                 //
        width: {default: 180},                                                  //
        disabledEqual: {type: Boolean},                                         //
        windowBoundary: {type: Boolean, default: true},                         //window
        disabled: {type: Boolean},                                              //
        beforeOpen: {type: Function},                                           //
        beforeHide: {type: Function},                                            //
    },
    computed: {
        popoverBinding() {
            return {
                trigger: this.trigger,
                disabledHideOnClickOutside: this.disabledHideOnClickOutside,
                disabledDestroyOnHide: this.disabledDestroyOnHide,
                disabledToggleOnClickReference: this.disabledToggleOnClickReference,
                initialized: this.initialized,
                direction: this.direction,
                align: this.align,
                animate: this.animate,
                arrow: this.arrow,
                offset: this.offset,
                height: this.height,
                width: this.width,
                disabledEqual: this.disabledEqual,
                windowBoundary: this.windowBoundary,
                disabled: this.disabled,
                beforeOpen: this.beforeOpen,
                beforeHide: this.beforeHide,
            }
        },
    }
}

export {
    PopoverMixin,
    POPOVER_TRIGGER,
    POPOVER_DIRECTION,
    POPOVER_ALIGN,
}